.wrap {
    position: relative;
    width: 360px;
    height: 740px;
    margin: auto;
}

.intro {
    height: 100%;
    background: url(../img/intro.png) no-repeat;
    background-size: auto 100%;
}

.gnb {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    .btn_first {
        position: relative;
        width: 45px;
        height: 50px;
    }
    .btn_middle {
        position: absolute;
        top: 0;
        left: 50%;
        width: 125px;
        height: 50px;
        margin-left: -62px;
    }
    .btn_last {
        position: absolute;
        top: 0;
        right: 0;
        width: 50px;
        height: 50px;
    }
}

.main {
    height: 100%;
    background: url(../img/main.png) no-repeat;
    background-size: auto 100%;

    .tab {
        position: absolute;
        top: 50px;
        left: 15px;
        right: 15px;
        height: 48px;

        .btn {
            position: relative;
            float: left;
            width: 33.33%;
            height: 100%;
        }
    }

    .main_menu {
        overflow: hidden;
        position: absolute;
        top: 115px;
        left: 15px;
        right: 15px;
        height: 163px;

        .btn {
            position: relative;
            float: left;
            width: 65px;
            height: 50px;
            margin: 0 0 62px 23px;

            &:nth-child(4n + 1) {
                margin-left: 0;
            }
        }
    }

    .btn_banner1 {
        position: absolute;
        top: 358px;
        left: 15px;
        width: 250px;
        height: 30px;
    }

    .btn_banner2 {
        position: absolute;
        top: 409px;
        left: 15px;
        width: 330px;
        height: 94px;
    }
    .btn_banner3 {
        position: absolute;
        top: 518px;
        left: 15px;
        width: 330px;
        height: 207px;
    }
}

.loan {
    position: relative;
    height: 100%;
    padding: 50px 15px 0;
    box-sizing: border-box;

    &.step1 {
        background: url(../img/loan.png) no-repeat;
        background-size: auto 50px;
    }
    .step_status {
        position: relative;
        height: 47px;
        margin: 0 -15px;
        padding: 0 15px;
        border-bottom: 1px solid #cacaca;

        .tit {
            height: 47px;
            line-height: 47px;
            font-size: 11px;
            font-weight: bold;
        }
        .num_area {
            position: absolute;
            top: 9px;
            right: 15px;

            .num {
                overflow: hidden;
                float: left;
                width: 29px;
                height: 29px;
                margin-left: 7px;
                border-radius: 50%;
                color: #888;
                text-align: center;
                line-height: 29px;
                font-size: 12.5px;

                &:first-child {
                    margin-left: 0;
                }
                &.current {
                    background: #ef8535;
                    color: #fff;
                }
            }
        }
    }
    .form {
        padding-top: 5px;

        .cell {
            overflow: hidden;
            margin-top: 20px;

            .tit {
                float: left;
                width: 110px;

                .inner {
                    display: inline-block;
                    line-height: 17.5px;
                    vertical-align: middle;
                }
                &:after {
                    content: '';
                    display: inline-block;
                    height: 45px;
                    vertical-align: middle;
                }
            }
            .inp_area {
                overflow: hidden;

                .inner {
                    float: left;
                    width: 60px;

                    &.inner_sel {
                        position: relative;
                        width: 62px;

                        &:after {
                            content: '';
                            position: absolute;
                            top: 20px;
                            right: 12px;
                            width: 9.5px;
                            height: 5.5px;
                            background: url(../img/ico_arrow.png) no-repeat;
                            background-size: 100%;
                        }
                    }
                }
                .dash {
                    overflow: hidden;
                    float: left;
                    width: 19px;
                    height: 45px;
                    background: url(../img/dash.png) no-repeat 50% 50%;
                    color: transparent;
                    font-size: 1px;
                    text-indent: -100px;
                }
            }
            .inp_text {
                width: 100%;
                height: 45px;
                padding-left: 12px;
                border: 1px solid #69b6df;
                border-radius: 8px;
                background: #ddf3fe;
                box-sizing: border-box;
                color: #212121;
                font-size: 13.5px;
            }
            .inp_select {
                width: 100%;
                height: 45px;
                padding-left: 5px;
                border: 1px solid #69b6df;
                border-radius: 8px;
                background: #ddf3fe;
                box-sizing: border-box;
                color: #212121;
                font-size: 13.5px;
            }
        }
    }
    .btn_area {
        position: absolute;
        bottom: 15px;
        left: 15px;
        right: 15px;

        .inner {
            float: left;
            width: 50%;
            padding-left: 5px;
            box-sizing: border-box;

            &:first-child {
                padding: 0 5px 0 0;
            }
        }
        .btn_cancel {
            width: 100%;
            height: 44px;
            border: 1px solid #c8c8c8;
            border-radius: 8px;
            color: #212121;
            font-size: 13.5px;
        }
        .btn_confirm {
            width: 100%;
            height: 44px;
            border-radius: 8px;
            background: #ef8535;
            color: #fff;
            font-size: 13.5px;
        }
    }
}